import React, { memo, useCallback, useEffect, useState } from "react";
import { LeftModalHeader, ScreenWrapper, UIButton, UIText } from "@true/ui";
import { FlatGrid } from "react-native-super-grid";
import { AppItem } from "./AppItem";
import { APP_ITEM_SIZE } from "./constant";
import { changeStatusBar } from "./utils";
import { Fonts, styled, useNavigation, useTheme } from "@true/core";
import { StyleSheet, View } from "react-native";
import { IC_OUTLINED_SEARCH, IC_OUTLINED_SETTINGS } from "@true/asset";

const LIST_MINIAPPS = ["booking", "inside", "policy", "moment", "run", "compliance", "asset", "officemap", "visitor"];
export default memo(() => {
  const theme = useTheme();
  const [search, setSearch] = useState("");
  const renderItem = useCallback(({ item }: { item: string }) => {
    return <AppItem item={item} />;
  }, []);

  const navigation = useNavigation();

  useEffect(() => {
    navigation.addListener("focus", () => {
      changeStatusBar("dark");
    });
  }, []);

  return <ScreenWrapper>
    <LeftModalHeader title={"Platform"} hideSeparator rightComponent={<View style={styles.mr16} >
      <UIButton source={IC_OUTLINED_SETTINGS} />
    </View>} />
    <View style={styles.contentContainer}>
      <SSeachView>
        <SIcon source={IC_OUTLINED_SEARCH} />
        <StyledTextInput placeholder={"Search for application name"} placeholderTextColor={theme.neutral2} onChangeText={setSearch}/>
      </SSeachView>
      <View style={{
        gap:4
      }}>
        <UIText.HeaderLarge>Digital HQ</UIText.HeaderLarge>
        <UIText.BodyMedium400 style={{
          color:theme.neutral2
        }}>True digital headquarter of your company</UIText.BodyMedium400>
      </View>
    </View>
    <FlatGrid
        style={{
          paddingHorizontal: 8,
          marginTop:20
        }}
        contentContainerStyle={{
          gap:24
        }}
        itemDimension={APP_ITEM_SIZE}
        spacing={8}
        data={LIST_MINIAPPS.filter((item) => item.includes(search))}
        renderItem={renderItem} />
  </ScreenWrapper>;
});

const styles = StyleSheet.create({
  contentContainer: {
    gap: 24,
    paddingHorizontal: 16
  },
  mr16: {
    marginRight: 16
  }
});
const SSeachView = styled.View`
  flex-direction: row;
  display: flex;
  padding: 8px 12px;
  gap: 8px;
  align-items: center;
  border-radius: 8px;
  border: 1px solid ${p => p.theme.neutral5};
  background-color: ${p => p.theme.neutral8};
`;
const SIcon = styled.Image`
  width: 24px;
  height: 24px;
  tint-color: ${p => p.theme.neutral2};
`;
const StyledTextInput = styled.TextInput`
  color: ${p => p.theme.neutral2};
  font-family: ${Fonts.Regular};
  font-size: 14px;
  line-height: 20px; /* 142.857% */
`;
